.journal_title{
    font-family: Novecento Sans Web,sans-serif;
    letter-spacing: 2px;
    text-transform: uppercase;
    max-width: 58%;
    text-decoration: none;
    color: #fff;
    background-color: #248a83;
    font-size: 21px;
    position: absolute;
    left: 0;
    padding-top: 0px;
    margin-top: 25px;
    &:hover {
        color: rgb(240, 240, 186);
    }
}

.blogpost-reel {
    position: relative;
    left: 70px;
    top: 70px;
}

.entry-meta {
    background-color: #248a83;
    color: #fff;
    display: inline-block;
    font-family: Novecento Sans Web,sans-serif;
    position: relative;
    margin-right: 40p;
    bottom: 10px;
    text-transform: uppercase;
    padding: 3px;
    left: 297px;
    bottom: 37px;
    width: 39%;
}
.entry-content {
width: 646px;
position: relative;
bottom: 14px;
}


